import React, { useState, useEffect, useRef } from 'react';
import { Upload, Camera, Folder, FileText, Brain, Command, Lock } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Sidebar, SidebarContent, SidebarGroup, SidebarGroupContent, SidebarGroupLabel, SidebarMenu, SidebarMenuButton, SidebarMenuItem, SidebarProvider } from '@/components/ui/sidebar';
import axios from 'axios';
import Modal from '@/components/ui/Modal';
import './home.css';

export default function Optidrive() {
  const [mousePosition, setMousePosition] = useState({ x: 0, y: 0 });
  const [modalMessage, setModalMessage] = useState('');
  const containerRef = useRef(null);

  useEffect(() => {
    const handleMouseMove = (event) => {
      if (containerRef.current) {
        const rect = containerRef.current.getBoundingClientRect();
        setMousePosition({
          x: event.clientX - rect.left,
          y: event.clientY - rect.top,
        });
      }
    };

    window.addEventListener('mousemove', handleMouseMove);
    return () => {
      window.removeEventListener('mousemove', handleMouseMove);
    };
  }, []);

  const handleUpload = async (event) => {
    const files = event.target.files;
    const formData = new FormData();
    for (let file of files) {
      formData.append('file', file);
    }
    //create a upload button

    try {
      await axios.post('http://localhost:4000/upload', formData);
      setModalMessage('Files uploaded successfully');
    } catch (error) {
      console.error('Error uploading files:', error);
      setModalMessage('Error uploading files');
    }
  };

  const handleCreateFolder = async () => {
    const folderName = prompt('Enter folder name:');
    if (folderName) {
      try {
        const response = await axios.post('http://localhost:4000/create-folder', { folder_name: folderName });
        setModalMessage(response.data.message);
      } catch (error) {
        console.error('Error creating folder:', error);
        setModalMessage('Error creating folder');
      }
    }
  };

  const handleFileExplorer = async () => {
    try {
      const response = await axios.get('http://localhost:4000/file-explorer');
      console.log('Files:', response.data);


    } catch (error) {
      console.error('Error fetching files:', error);
      setModalMessage('Error fetching files');
    }
  };

  const handleOpenCamera = async () => {
    const video = document.createElement('video');
    video.width = 640;
    video.height = 480;
    document.body.appendChild(video);

    try {
      const stream = await navigator.mediaDevices.getUserMedia({ video: true });
      video.srcObject = stream;
      video.play();

      const mediaRecorder = new MediaRecorder(stream);
      const chunks = [];
      mediaRecorder.ondataavailable = (event) => {
        chunks.push(event.data);
      };
      mediaRecorder.onstop = async () => {
        const blob = new Blob(chunks, { type: 'video/mp4' });
        const formData = new FormData();
        formData.append('file', blob, 'video.mp4');
        try {
          await axios.post('http://localhost:4000/upload', formData);
          setModalMessage('Video uploaded successfully');
        } catch (error) {
          console.error('Error uploading video:', error);
          setModalMessage('Error uploading video');
        }
      };

      setTimeout(() => {
        mediaRecorder.stop();
        stream.getTracks().forEach(track => track.stop());
        document.body.removeChild(video);
      }, 5000); // Record for 5 seconds
      mediaRecorder.start();
    } catch (error) {
      console.error('Error accessing camera:', error);
    }
  };

  return (
      <SidebarProvider>
        <div className="optidrive-container" ref={containerRef}>
          <Sidebar className="sidebar">
            <SidebarContent>
              <SidebarGroup>
                <SidebarGroupLabel className="sidebar-group-label">Tools</SidebarGroupLabel>
                <SidebarGroupContent>
                  <SidebarMenu>
                    <SidebarMenuItem>
                      <SidebarMenuButton className="sidebar-menu-button">
                        <Brain />
                        <span>Analyze with AI</span>
                      </SidebarMenuButton>
                    </SidebarMenuItem>
                    <SidebarMenuItem>
                      <SidebarMenuButton className="sidebar-menu-button">
                        <Command />
                        <span>Command Center</span>
                      </SidebarMenuButton>
                    </SidebarMenuItem>
                    <SidebarMenuItem>
                      <SidebarMenuButton className="sidebar-menu-button">
                        <Lock />
                        <span>Lock</span>
                      </SidebarMenuButton>
                    </SidebarMenuItem>
                  </SidebarMenu>
                </SidebarGroupContent>
              </SidebarGroup>
            </SidebarContent>
          </Sidebar>
          <main className="main-content">
            <h1>Optidrive</h1>
            <div className="action-grid">
              <Button className="action-button" onClick={() => document.getElementById('file-upload').click()}>
                <Upload />
                Upload
              </Button>
              <Button className="action-button" onClick={handleOpenCamera}>
                <Camera />
                Open Camera
              </Button>
              <Button className="action-button" onClick={handleCreateFolder}>
                <Folder />
                Create Folder
              </Button>
              <Button className="action-button" onClick={handleFileExplorer}>
                <FileText />
                File Explorer
              </Button>
            </div>
            <div className="drop-zone">
              <p>Drag and drop files here</p>
              <Input type="file" className="hidden" id="file-upload" multiple onChange={handleUpload} />
              <label htmlFor="file-upload" className="file-input-label">
                Select Files
              </label>
            </div>
          </main>
          <div
              className="glow-effect"
              style={{
                '--mouse-x': `${mousePosition.x}px`,
                '--mouse-y': `${mousePosition.y}px`,
              }}
          />
          {modalMessage && <Modal message={modalMessage} onClose={() => setModalMessage('')} />}
        </div>
      </SidebarProvider>
  );
}